import React, { useState, useEffect } from 'react';
import { UserProfile, DailyRecord } from './types';
import { loadUserProfile, loadDailyRecords } from './utils/storage';
import Auth from './components/Auth';
import { getCurrentUser, logout } from './services/bmob';
import { calculateMetrics } from './utils/calculations';
import UserProfileComponent from './components/UserProfile';
import DailyLogger from './components/DailyLogger';
import Dashboard from './components/Dashboard';
import TodoList from './components/TodoList';
import Store from './components/Store';
import NotificationCenter from './components/NotificationCenter';
import { format } from 'date-fns';
import { User, Calendar, BarChart3, CheckSquare, ShoppingBag } from 'lucide-react';

import { ToastProvider } from './components/Toast';

type TabType = 'profile' | 'logger' | 'dashboard' | 'todo' | 'store';

function App() {
  const [userProfile, setUserProfile] = useState<UserProfile | null>(null);
  const [dailyRecords, setDailyRecords] = useState<DailyRecord[]>([]);
  const [activeTab, setActiveTab] = useState<TabType>('profile');
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [userMetrics, setUserMetrics] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState<any | null>(getCurrentUser());

  useEffect(() => {
    if (!user) {
      setUserProfile(null);
      setDailyRecords([]);
      setUserMetrics(null);
      return;
    }

    (async () => {
      setLoading(true);
      try {
        const profile = await loadUserProfile();
        const records = await loadDailyRecords();
        setUserProfile(profile);
        setDailyRecords(records);
        if (profile) {
          const metrics = calculateMetrics(profile);
          setUserMetrics(metrics);
          //  tab 
          // isProfileCompleted === false
          // undefinedtrue
          if (activeTab === 'profile' && profile.isProfileCompleted !== false) {
             setActiveTab('logger');
          }
        }
      } catch (e) {
        console.error('Failed to load user data:', e);
      } finally {
        setLoading(false);
      }
    })();
  }, [user]); //  user 

  const handleLogout = () => {
    logout();
    setUser(null);
    window.location.reload(); // 
  };

  const handleProfileUpdate = (profile: UserProfile) => {
    setUserProfile(profile);
    const metrics = calculateMetrics(profile);
    setUserMetrics(metrics);
    setActiveTab('logger');
  };

  const tabs = [
    { id: 'profile' as TabType, label: '', icon: User },
    { id: 'logger' as TabType, label: '', icon: Calendar },
    { id: 'dashboard' as TabType, label: '', icon: BarChart3 },
    { id: 'todo' as TabType, label: '', icon: CheckSquare },
    { id: 'store' as TabType, label: '', icon: ShoppingBag }
  ];

  if (!user) {
    return (
      <ToastProvider>
        <Auth onSuccess={() => { setUser(getCurrentUser()); }} />
      </ToastProvider>
    );
  }

  return (
    <ToastProvider>
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <h1 className="text-2xl font-bold text-gray-900"></h1>
            <div className="flex items-center gap-4">
                {user && (
                    <NotificationCenter onProfileUpdate={handleProfileUpdate} />
                )}
                <button
                    onClick={handleLogout}
                    className="px-3 py-2 text-sm bg-gray-100 hover:bg-gray-200 rounded-md"
                ></button>
            </div>
          </div>
        </div>
      </header>

      {/* Desktop Navigation */}
      <nav className="bg-white border-b hidden md:block">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center w-full">
            {/* Left Tabs */}
            <div className="flex space-x-8">
              {tabs.filter(t => t.id !== 'store').map(tab => {
                const Icon = tab.icon;
                return (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`flex items-center px-3 py-4 text-sm font-medium border-b-2 transition-colors ${
                      activeTab === tab.id
                        ? 'border-blue-500 text-blue-600'
                        : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                    }`}
                  >
                    <Icon className="mr-2" size={16} />
                    {tab.label}
                  </button>
                );
              })}
            </div>

            {/* Right Tabs (Store) */}
            <div className="flex">
              {tabs.filter(t => t.id === 'store').map(tab => {
                const Icon = tab.icon;
                return (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`flex items-center px-3 py-4 text-sm font-medium border-b-2 transition-colors ${
                      activeTab === tab.id
                        ? 'border-orange-500 text-orange-600'
                        : 'border-transparent text-orange-500 hover:text-orange-700 hover:border-orange-300'
                    }`}
                  >
                    <Icon className="mr-2" size={16} />
                    {tab.label}
                  </button>
                );
              })}
            </div>
          </div>
        </div>
      </nav>

      {/* Mobile Navigation */}
      <div className="fixed bottom-0 left-0 w-full bg-white border-t z-50 flex justify-around items-center h-16 pb-[env(safe-area-inset-bottom)] md:hidden shadow-[0_-1px_3px_rgba(0,0,0,0.05)]">
        {tabs.map(tab => {
          const Icon = tab.icon;
          const isActive = activeTab === tab.id;
          return (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex flex-col items-center justify-center w-full h-full space-y-1 ${
                isActive ? (tab.id === 'store' ? 'text-orange-500' : 'text-blue-500') : 'text-gray-400'
              }`}
            >
              <Icon size={24} strokeWidth={isActive ? 2.5 : 2} />
              <span className="text-[10px] font-medium">{tab.label}</span>
            </button>
          );
        })}
      </div>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 pb-24 md:pb-8">
        {loading && (
          <div className="fixed inset-0 bg-black/20 flex items-center justify-center z-50">
            <div className="bg-white rounded-md px-6 py-4 text-gray-700 shadow">
              ...
            </div>
          </div>
        )}
        {activeTab === 'profile' && (
          <UserProfileComponent
            profile={userProfile}
            onProfileUpdate={handleProfileUpdate}
          />
        )}

        {activeTab === 'logger' && userProfile && userMetrics && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            <div className="lg:col-span-2">
              <DailyLogger
                userProfile={userMetrics}
                selectedDate={selectedDate}
                onDateChange={setSelectedDate}
              />
            </div>
            <div className="space-y-6">
              {/* Quick Stats */}
              <div className="bg-white rounded-lg shadow-md p-6">
                <h3 className="text-lg font-semibold text-gray-800 mb-4"></h3>
                <div className="space-y-3">
                  <div className="flex justify-between">
                    <span className="text-gray-600">:</span>
                    <span className="font-medium">{userProfile.weight} kg</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">BMI:</span>
                    <span className="font-medium">{userMetrics.bmi.toFixed(1)} ({userMetrics.bmiCategory})</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">BMR:</span>
                    <span className="font-medium">{userMetrics.bmr.toFixed(0)} kcal</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">TDEE:</span>
                    <span className="font-medium">{userMetrics.tdee.toFixed(0)} kcal</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">:</span>
                    <span className="font-medium text-blue-600">{userMetrics.dailyCalorieLimit.toFixed(0)} kcal</span>
                  </div>
                </div>
              </div>

              {/* Quick Tips */}
              <div className="bg-white rounded-lg shadow-md p-6">
                <h3 className="text-lg font-semibold text-gray-800 mb-4"></h3>
                <div className="space-y-2 text-sm text-gray-600">
                  <p> </p>
                  <p> 500kcal0.5kg</p>
                  <p> BMR</p>
                  <p> </p>
                  <p> </p>
                </div>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'dashboard' && userProfile && userMetrics && (
          <Dashboard
            dailyRecords={dailyRecords}
            userProfile={userMetrics}
          />
        )}

        {activeTab === 'todo' && userProfile && (
          <TodoList
            selectedDate={selectedDate}
            onDateChange={setSelectedDate}
          />
        )}

        {activeTab === 'store' && userProfile && (
          <Store />
        )}

        {!userProfile && activeTab !== 'profile' && (
          <div className="text-center py-12">
            <div className="text-gray-500 mb-4">
              <User size={48} className="mx-auto mb-4" />
              <h3 className="text-lg font-medium mb-2"></h3>
              <p className="mb-4"></p>
              <button
                onClick={() => setActiveTab('profile')}
                className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition-colors"
              >
                
              </button>
            </div>
          </div>
        )}
      </main>
    </div>
    </ToastProvider>
  );
}

export default App;
